.form {
  border: 1px solid #e7e8ec;
  padding: 20px 40px;
  margin-top: 10px;

  &.hide {
    display: none; }

  .label {
    font-weight: bold;
    font-size: 16px;
    margin-bottom: 20px; }

  .item {
    margin-bottom: 2px;

    textarea {
      vertical-align: top;
      resize: vertical;
      padding: 6px;
      width: 50%; }

    label {
      display: inline-block;
      min-width: 150px; } }

  button {
      padding: 10px;
      max-width: 200px;
      display: block;
      background: var(--button-blue);
      color: white;
      font-size: 14px;
      margin-left: 150px; } }
